import {Component, OnInit} from '@angular/core';
import {AlertController, ModalController} from '@ionic/angular';
import {SettingsStore} from '../../store/settings.store';
import {ICategory} from '../../model/category.model';
import {IBrand} from '../../model/brend.model';
import {IModel} from '../../model/model.model';
import {PinpointHelper} from '../../helper/pinpoint.helper';
import {PhotoHelper} from '../../helper/photo.helper';
import {ISoldCreate} from '../../model/sold.model';

import * as Parse from 'parse';
import {ParseHelper} from '../../helper/parse.helper';
import {PhotoServices} from '../../service/photo.services';
import {SoldService} from '../../service/sold.service';

@Component({
  selector: 'app-sold',
  templateUrl: './sold.page.html',
  styleUrls: ['./sold.page.scss'],
})
export class SoldPage implements OnInit {
  uploaded: boolean;
  step: number;
  categories: ICategory[];
  brands: IBrand[];
  models: IModel[];
  imgArr: string[];
  sold: ISoldCreate;

  constructor(private soldService: SoldService, private photoService: PhotoServices, private photoHelper: PhotoHelper, private modalController: ModalController, private settingsStore: SettingsStore, private pinpointHelper: PinpointHelper, private alertController: AlertController) {
    // @ts-ignore
    this.sold = {
      billNumber: null,
      soldDate: null,
      brand: null,
      model: null,
      billPhotos: [],
      category: null,
      user: Parse.User.current()
    };
  }

  ngOnInit() {
    this.imgArr = [];
    this.step = 0;
    this.categories = this.settingsStore.categories;
    this.models = [];
    this.brands = this.settingsStore.brands;
  }

  selectBrand(brand: string) {
    console.log('Checking brand: ', brand);
    this.sold.brand = ParseHelper.getParseObjectWithoutData('Brand', brand);
    this.categories = this.pinpointHelper.getCategoriesFromModel(brand);
    console.log('Categroies are: ', this.categories);
    this.step++;
  }

  selectCategory(id: string) {
    this.sold.category = ParseHelper.getParseObjectWithoutData('Category', id);
    this.models = this.pinpointHelper.getModelsFromCategory(id, this.sold.brand.id, Parse.User.current().get('shopBrand').id);
    this.step++;
  }

  async closeModal() {
    await this.modalController.dismiss();
  }

  selectModel(id: string) {
    this.sold.model = ParseHelper.getParseObjectWithoutData('Model', id);
    this.step++;
  }

  async addPhoto() {
    if (this.imgArr.length >= 1) {
      return;
    }
    const result = await this.photoHelper.takePhoto(75);
    this.imgArr[0] = result;
    console.log(this.imgArr.length);
    const allowedPics = 1 - this.imgArr.length;
    // const alert = await this.alertController.create({
    //   header: 'Add listing photo',
    //   message: 'Would you like to add your photo from the camera or library?',
    //   buttons: [
    //     {
    //       text: 'Camera',
    //       cssClass: 'primary',
    //       handler: async () => {
    //         const result = await this.photoHelper.takePhoto(75);
    //         this.imgArr[0] = result;
    //       }
    //     }, {
    //       text: 'Library',
    //       cssClass: 'primary',
    //       handler: async () => {
    //         const result = await this.photoHelper.addPhoto(allowedPics, 1, false);
    //         if(result){
    //           this.imgArr[0] = result;
    //         }
    //       }
    //     }
    //   ]
    // });
    //
    // await alert.present();
  }

  removePhoto(i: number) {
    this.imgArr.splice(i, 1);
  }


  async finishUpload() {
    try {
      console.log(' Got data: ', this.sold);
      this.uploaded = false;
      if (this.imgArr.length === 1) {
        this.sold.billPhotos[0] = await this.photoService.savePhoto(this.imgArr[0]);
        // @ts-ignore
        this.sold.soldDate = new Date(this.sold.soldDate);
        await this.soldService.saveSold(this.sold);
        const delay = ms => new Promise(res => setTimeout(res, ms));
        await delay(1000);
        this.uploaded = true;
        console.log("UPLOADED");
        this.closeModal();
      } else {
        this.imgArr = [];
        this.sold.billPhotos = [];
        this.uploaded = true;
        const alert = await this.alertController.create({
          header: 'Create Listing',
          message: 'Fix error before submitting the listing.',
          buttons: ['OK']
        });
        await alert.present();

      }
    } catch (e) {
      this.imgArr = [];
      this.sold.billPhotos = [];
      const delay = ms => new Promise(res => setTimeout(res, ms));
      await delay(1000);
      this.uploaded = true;
      const alert = await this.alertController.create({
        header: 'Sold',
        message: 'An error has occurred please try again later.',
        buttons: ['OK']
      });
      await alert.present();
      console.log('Error: ', e);
    }

  }

  previousPage() {
    if (this.step !== 0) {
      this.step--;
    }
  }
}
