import {Injectable} from '@angular/core';
import {ImagePicker} from '@ionic-native/image-picker/ngx';
import {PermissionHelper} from './permission.helper';
// import {Camera} from '@ionic-native/camera/ngx';

import { Camera, CameraResultType, CameraSource } from '@capacitor/camera';


@Injectable({
  providedIn: 'root'
})

export class PhotoHelper {
  constructor(private permissionHelper: PermissionHelper, private imagePicker: ImagePicker) {
  }

  async addPhoto(limit: number, type: number, video: boolean): Promise<string> {
    const imgArr = [];
    if (await this.permissionHelper.hasReadPermission()) {
      await this.imagePicker.getPictures({
        maximumImagesCount: limit,
        outputType: type,
        allow_video: video,
      }).then((results) => {
        for (const r of results) {

          imgArr.push(r);
        }
      }, (err) => {
        console.log('Error: ', err);
      });
      if (imgArr) {
        return imgArr[0];
      }
    }
    //console.log('Img arr is:', imgArr);

  }

  async takePhoto(quality) {
    const result = await Camera.getPhoto({
      quality: quality,
      resultType: CameraResultType.Base64,
      source: CameraSource.Prompt,
      webUseInput: true
    });
    console.log(result);
    const reader = new FileReader()
    // reader.readAsDataURL(result.webPath)
    return result.base64String;

  }
}
