import {Injectable} from '@angular/core';
import * as Parse from 'parse';


@Injectable({
  providedIn: 'root'
})

export class PhotoServices {
  constructor() {
  }

  async b64toBlob(base64Data) {

    const base64Response = await fetch(`data:image/jpeg;base64,${base64Data}`);
    const blob = await base64Response.blob();
    return blob;
  }

  async savePhoto(data) {
    try {
      console.log('Saving photo');
      console.log('Photo is: ', data);
      const final = await this.b64toBlob(data);
      const myPhoto = new Parse.File('location.jpg', final);
      const savedPhoto = await myPhoto.save();
      return savedPhoto.url();
    } catch (e) {
      console.log(e);
    }
  }
}
